import React from "react";
import styled from "styled-components";

import { imageSizes } from "theme";
import PageContent from "components/PageContent";
import Image from "components/Image";

const HomePageContent = styled(PageContent)`
  display: grid;
  grid-gap: var(--grid-gutter);
  grid-template-columns: repeat(1, 1fr);

  @media ${({ theme }) => theme.devices.small.query} {
    grid-template-columns: repeat(2, 1fr);
  }

  @media ${({ theme }) => theme.devices.medium.query} {
    grid-gap: calc(var(--grid-gutter) * 2);
  }

  @media ${({ theme }) => theme.devices.large.query} {
    grid-template-columns: repeat(3, 1fr);
  }
`;

const BlockMedia = styled.div`
  width: 100%;
  padding-top: ${({ ratio }) => (1 / ratio) * 100}%;
  position: relative;
  overflow: hidden;

  img,
  .lazyload-placeholder {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: none;
    user-select: none;
  }
`;

const BlockTitle = styled.h1``;
const BlockText = styled.p``;

const Block = styled.article`
  &.card {
    border-radius: 2px;
    overflow: hidden;
    background-color: #fff;
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14),
      0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.2);

    ${BlockTitle}, ${BlockText} {
      padding: 0 var(--grid-gutter);
    }
  }

  &:not(.card) ${BlockMedia} {
    border-radius: 2px;
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14),
      0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.2);
  }

  &.large {
    @media ${({ theme }) => theme.devices.small.query} {
      grid-column-end: span 2;
    }
  }

  &.main {
    @media ${({ theme }) => theme.devices.small.query} {
      grid-column-end: span 2;
    }

    @media ${({ theme }) => theme.devices.large.query} {
      grid-column-end: span 3;

      display: grid;
      grid-column-gap: var(--grid-gutter);
      grid-template-columns: repeat(3, 1fr);

      @media ${({ theme }) => theme.devices.medium.query} {
        grid-column-gap: calc(var(--grid-gutter) * 2);
      }

      ${BlockMedia} {
        grid-column: 1;
        grid-row: 1 / 5; /* 5 elements in block should be enouth */
      }

      ${BlockTitle}, ${BlockText} {
        margin-top: 0;
        grid-column: 2 / 4;
      }
    }
  }
`;

const blockImageSize = {
  normal: imageSizes(
    {
      xLarge: width => Math.floor((width - 32 * 4) / 3) + "px",
      large: width => Math.floor((width - 32 * 4) / 3) + "px",
      medium: width => Math.floor((width - 32 * 3) / 2) + "px",
      small: width => Math.floor((width - 16 * 3) / 2) + "px",
    },
    `calc(100vw - 16px * 2)`
  ),
  main: imageSizes(
    {
      xLarge: width => Math.floor((width - 32 * 4) / 3) + "px",
      large: width => Math.floor((width - 32 * 4) / 3) + "px",
      medium: width => Math.floor((width - 32 * 3) / 2) * 2 + 32 + "px",
      small: width => Math.floor(width - 16 * 2) + "px",
    },
    `calc(100vw - 16px * 2)`
  ),
};

